import { Component, OnInit, ElementRef, ViewChild  } from '@angular/core';
import { IonContent, ModalController } from '@ionic/angular';
import { CameraPreview, CameraPreviewPictureOptions, CameraPreviewOptions, CameraPreviewDimensions } from '@ionic-native/camera-preview/ngx';
import { Router, NavigationExtras } from '@angular/router';

@Component({
  selector: 'app-camera-view',
  templateUrl: './camera-view.page.html',
  styleUrls: ['./camera-view.page.scss'],
})
export class CameraViewPage implements OnInit {
// 'plug into' DOM canvas element using @ViewChild
@ViewChild('canvas',{static:false})canvasEl : ElementRef;
@ViewChild(IonContent,{static:true}) content: IonContent;
// Reference Canvas object
private _CANVAS  : any;
// Reference the context for the Canvas element
private _CONTEXT : any;
picture:any="";
cameraState:Boolean = false;
confirm_photo:Boolean =false;

  constructor(private cameraPreview: CameraPreview, private router:Router) {
   // melhorar fluxo da camera
   }
  ngOnInit(){}
   async ionViewWillEnter() {
    //#check if is there a picture 
    if(true/* (this.picture.size!==0) */){
      //CAMERA
      const cameraPreviewOpts: CameraPreviewOptions = {
        x: 0,
        y: 0,
        width: window.screen.width,
        height: window.screen.height,
        camera: 'rear',
        tapPhoto: true,
        previewDrag: true,
        toBack: true,
        alpha: 1
      }
      this.cameraState = true
      
      // start camera
      await this.cameraPreview.startCamera(cameraPreviewOpts).then(
        (res) => {
        this.drawCircle();
          //alert(res);
        },
        (err) => {
          console.log(err)
          confirm("Erro na câmera!")
          this.router.navigate(['home']);
        });
    }
  }
  

  ionViewDidEnter(){
    this._CANVAS 	    = this.canvasEl.nativeElement;
    this._CANVAS.width  	= window.innerWidth     
    this._CANVAS.height 	= window.innerHeight  
    this.initialiseCanvas();
  }
  initialiseCanvas(){
    if(this._CANVAS.getContext)
    {
      
        this.setupCanvas();
    }
  }

  setupCanvas(){
    this._CONTEXT = this._CANVAS.getContext('2d');
    this.refresh();
  }

  refresh(){
    this.drawCircle()
  }

  drawCircle(){
    this._CONTEXT.beginPath();

    // x, y, radius, startAngle, endAngle
    this._CONTEXT.arc(this._CANVAS.width/2, this._CANVAS.height/2, 150, 0, 2 * Math.PI);
    this._CONTEXT.lineWidth = 4;
    this._CONTEXT.strokeStyle = '#0000FF'; /* '#ffffff' */
    this._CONTEXT.stroke();
    
      
  }

  
  // //CAMERA
  // // Set the handler to run every time we take a picture
  // this.cameraPreview.setOnPictureTakenHandler().subscribe((result) => {
  //   console.log(result);
  //   // do something with the result
  // });
 

  takePicture(){
    
    // picture options
    const pictureOpts: CameraPreviewPictureOptions = {
      
      width: 1280,
      height: 1280,
      quality: 85
    }
    
    // take a picture
    this.cameraPreview.takePicture(pictureOpts).then((imageData) => {
      this.picture = 'data:image/jpeg;base64,' + imageData;
      
      
      //this.cameraPreview.hide();
      this.cameraPreview.stopCamera();
      //this.content.setElementStyle("background-color","black")
      //confirm(this._CANVAS.style.display);
      
    }, (err) => {
      console.log(err);
      confirm("Erro na câmera!")
      this.router.navigate(['photo-register']);
    });
    //this._CANVAS.style.display="hidden";
    
    this.cameraState = false
  }  
  cancel(){
    this.router.navigateByUrl('photo-register');
    
  }
  use_photo(){
    this.confirm_photo = true;
    //this.cameraState = false;
    //this.picture="";
    // const toast = this.toastCtrl.create({
    //   message: 'Photo was added successfully',
    //   duration: 1500,
    //   position: 'top'
    // });
    // toast.present();
   //this.reset_camera_preview();
   
    let picture_meal: NavigationExtras = {
      queryParams: {
        photo: JSON.stringify(this.picture)
      }
    }
    this.router.navigate(['photo-register'], picture_meal);
  }
  reset_camera_preview(){
    //this._CANVAS.style.display="block";
    
    this.picture="";
    this.refresh();
    this.cameraState= true;
    this.cameraPreview.show()
    
    //this.content.setElementStyle("background-color","transparent")
  }
  cameraSwitch(){  
    // Switch camera
    this.cameraPreview.switchCamera();
  } 
  effect(){ 
    // set color effect to negative
    this.cameraPreview.setColorEffect('negative');
  }
  terminateCamera(){  
    // Stop the camera preview
    this.cameraPreview.stopCamera();
    this.router.navigateByUrl('photo-register');
    // const rootElement = <HTMLElement>document.getElementsByTagName('html')[0];
    // rootElement.classList.remove('camera-open');
  }
  

}

